import React, { useEffect, useState } from "react";
import { useMetaMask } from "metamask-react";
import { abi } from './abi';
import { nftContractAddress } from "./config";
import Input from "./input"
import Owned from "./owned"
import "./app.css"
const ethers = require('ethers');
const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
let contractAddress = nftContractAddress;
const signer = provider.getSigner();
let contract = new ethers.Contract(contractAddress, abi, provider);
let contractSigner = contract.connect(signer);
let gasPrice = provider.getGasPrice()
let overrides = {
	gasLimit: 300000,
	gasPrice: gasPrice,
	value: ethers.utils.parseEther('0.4'),
};

function App() {
	const { status, connect, account, chainId, ethereum } = useMetaMask();
	const [nftAmount, setNftAmount] = useState(1);
	const [totalOwned, setTotalOwned] = useState(0);
	const [update, setUpdate] = useState(Date.now())
	const publicMint = () => {
		let value = 0.4 * nftAmount
		overrides.value = ethers.utils.parseEther(String(value))
		contractSigner.publicMint(nftAmount, overrides)
	}
	const ownedAmount = async (account) => {
		let balance = await contract.balanceOf(account)
		let result = balance.toString()
		return result
	}
	const checkTotal = () => {
		if (status === "connected") {
			new Promise((resolve, reject) => {
				let value = ownedAmount(account)
				resolve(value)
			})
				.then(data => {
					setTotalOwned(data)
				})
		}
	}
	useEffect(() => {
		checkTotal()
	}, [status])
	useEffect(() => {
		clearTimeout()
		setTimeout(() => {
			checkTotal()
			setUpdate(Date.now())
		}, 1000)
	}, [update])

	if (status === "initializing") return(
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src="https://hype-labs.digital/Unrevealed-RonaS2.mp4" type="video/mp4" />
			</video>
			<div>Synchronisation with MetaMask ongoing...</div>
		</div>
	) 

	if (status === "unavailable") return <div>MetaMask not available :(</div>

	if (status === "notConnected") return (
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src="https://hype-labs.digital/Unrevealed-RonaS2.mp4" type="video/mp4" />
			</video>
			<button onClick={connect}>Connect to MetaMask</button>
		</div>
	)

	if (status === "connecting") return (
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src="https://hype-labs.digital/Unrevealed-RonaS2.mp4" type="video/mp4" />
			</video>
			<div>Connecting...</div>
		</div>
	)

	if (status === "connected") return (
		<div className="mainContainer">
			<video autoPlay muted loop id="myVideo">
				<source src="https://hype-labs.digital/Unrevealed-RonaS2.mp4" type="video/mp4" />
			</video>
			<div className="mintContainer">
				<Owned totalOwned={totalOwned} setTotalOwned={setTotalOwned} />
				<Input setNftAmount={setNftAmount} nftAmount={nftAmount} />
				<button className="mintBtn" onClick={publicMint}>Mint</button>
			</div>
		</div>
	)

	return <div></div>;
}
export default App