import React from "react";
const Input = (props) => {
    const handleChange = (e) => {
        props.setNftAmount(e.target.value)
    }
    return (
        <div>
            <input type="number" min="1" max="10" step="1" className="nftAmountInput" onChange={handleChange} value={props.nftAmount}></input>
        </div>
    )
}
export default Input